import * as React from "react";
import styleScss from "./Star.module.scss";

interface Prop {
  count: number;
}

export const Star: React.FC<Prop> = (prop) => {
  let area: Number[] = [];
  for (let index = 0; index < prop.count; index++) {
    area.push(index + 1);
  }

  return (
    <>
      {area.map((i, index) => {
        return <div key={index} className={styleScss["layer" + i]}></div>;
      })}
    </>
  );
};
