import styles from "./App.module.css";
import {
  HashRouter as Router,
  Route,
  Routes,
  Navigate,
} from "react-router-dom";
import { Home, Login, Err404, HomePage, Love } from "./view";
import { useEffect } from "react";
import { useSelector } from "./redux/hooks";

// 修改页面标签栏
function Title({ title, el }) {
  useEffect(() => {
    document.title = title;
    return () => {
      document.title = "加载中...";
    };
  }, [title]);
  return el;
}

// 路由守卫组件 isAuthenticated 判断当前是否符合条件
function PrivateRoute({ isAuthenticated }) {
  return isAuthenticated ? <Home /> : <Navigate to="/login" />;
}

function App() {
  // 获取相关技术页面列表
  const list = useSelector((state) => state.constant.list);
  return (
    <Router>
      <div className={styles.App}>
        <Routes>
          <Route
            path={"/"}
            element={<PrivateRoute isAuthenticated={true} />}
          >
            <Route
              path="/"
              element={<Title title={"首页"} el={<HomePage />} />}
            />
            {/* 循环列表产生路由 */}
            {list.map((item, index) => {
              return (
                <Route
                  key={index}
                  path={item.url}
                  element={<Title title={item.title} el={<item.page />} />}
                />
              );
            })}
          </Route>
          <Route path={"/love"} element={<Title title={"dddd"} el={<Love />} />} />
          <Route
            path={"/login"}
            element={<Title title={"登录注册页面"} el={<Login />} />}
          />
          <Route path="*" element={<Err404 />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
